@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,700;1,300;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* CSS Document for Screens */


body {
  font-family: 'Open Sans', sans-serif; 
  /*font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;*/
  font-size: 16px;
  color: #2D3047;
}

button {
  padding: 5px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 13px;
  background-color: #1B998B;
  background-image: radial-gradient(#1B998B, #000);
  
  color: white;
  border-radius: 8px;
  cursor: pointer;
}



h3, h4 {
  margin-bottom: 0;
}

nav {
  border-bottom: 4px solid #fff;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background-image: linear-gradient(#1B998B, #fff);
  text-shadow: 1px 2.5px #1B998B;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: #FFF;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background-image: radial-gradient(#1B998B, #fff);
  color: #fff;

}

#page-body {
  max-width: 700px;
  margin: auto;
  padding: 80px 5px 0 5px;
}

.article-list-item {
  text-decoration: none;
  color: #505050;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 250px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 270px;
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

